.productionProcess {
  margin-bottom: 250px;
  position: relative;

  @media screen and (max-width: 991px) {
    margin-bottom: 100px;
  }
}

.purpleWaveContainer {
  position: absolute;
  width: 100%;
  min-width: 1700px;
  left: 50%;
  transform: translateX(-50%) scale(2);
}

.whyImageContainer {
  position: absolute;
  max-width: 1200px;
  width: 100%;
  right: -100px;
  top: 500px;
  z-index: -1;

  transition: opacity 0.3s ease;
  @media screen and (max-width: 991px) {
    opacity: 0;
  }

  img {
    max-width: 1199px;
  }
}

.dataWrapper {
  position: relative;
  display: flex;
  z-index: 1;

  @media screen and (max-width: 991px) {
    flex-wrap: wrap;
  }
}

.dataContainer {
  padding: 10px;
  transition: transform 0.3s ease;
  cursor: default;

  &:hover {
    transform: scale(1.1);

    @media screen and (max-width: 991px) {
      transform: scale(1.03);
    }

    .dataInner {
      &:before {
        opacity: 1;
      }
    }
  }

  @media screen and (max-width: 991px) {
    width: 100% !important;
  }
}

.dataInner {
  &:before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: red;
    top: 0;
    left: 0;
    border-radius: 10px;
    background: linear-gradient(108deg, #FF62A7, #FEAA84);
    opacity: 0;
    transition: opacity 0.3s ease;
    // z-index: -1;
  }

  position: relative;
  background: linear-gradient(108deg, #58BFB8, #83D8A8);
  color: white;
  border-radius: 10px;
  padding: 15px;
  height: 100%;

  h3 {
    position: relative;
    color: white;
  }

  p {
    position: relative;
    color: white;
    font-size: 0.875rem;
  }

  hr {
    position: relative;
    background-color: white;
    height: 2px;
  }
}

.tagline,
.tagline2 {
  margin: 0 auto 20px auto !important;
  text-align: center;
  z-index: 1;
  position: relative;

  button {
    border: none;
    border-radius: 0.5rem;
    span {
      font-weight: bold;
      text-shadow: 0 3px 5px rgba(black, 0.24);
    }
  }
}

.tagline2 {
  margin-top: 100px !important;
}

.curveContainer {
  position: absolute;
  bottom: -50px;
  right: -25px;
  width: 50px;

  @media screen and (max-width: 991px) {
    bottom: -10px;
    right: -25px;

    img {
      transform: scaleY(-1) rotate(-90deg);
    }
  }
}